import { BUTTON } from '../../constance';
import { edit_BtnClick, delete_BtnClick } from './index.js';
export default function onHeadInner_BtnClick(props, key, record, index, e) {
	// console.log(arguments, 22);
	// console.log(e.nativeEvent, '0000');
	// e.stopPropagation();
	// e.nativeEvent.stopPropagation();
	// e.nativeEvent.stopImmediatePropagation();
	// e.preventDefault();
	switch (key) {
		case BUTTON.delete:
			delete_BtnClick.bind(this)(props, index);
			break;
		case BUTTON.edit:
			edit_BtnClick.bind(this)(props, index);
			break;
	}
	// return false;
}
