import { AREA, STATUS, URL } from '../../constance';
import { addRow_BtnClick, btnVisible_BtnClick } from './index.js';
export default function add_BtnClick(props) {
	setTimeout(() => {
		let headIndexs = [];
		let olddata = props.editTable.getAllRows(AREA.headTableArea);
		let newdata = olddata.filter((item) => {
			return item.status != 3;
		});
		for (let index = 0; index < newdata.length; index++) {
			headIndexs.push(index);
		}
		// props.editTable.setTableData(AREA.headTableArea, { rows: [] });
		props.editTable.addRow(AREA.headTableArea);
		props.editTable.setEditableRowByIndex(AREA.headTableArea, headIndexs, false);
		this.state.index = headIndexs.length;
		// props.linkTo('../list/index.html', {
		// 	index: headIndexs.length
		// });
		props.editTable.setTableData(AREA.bodyTableArea, { rows: [] });
		addRow_BtnClick.bind(this)(props);
		btnVisible_BtnClick.bind(this)(props);
		props.editTable.focusRowByIndex(AREA.headTableArea, headIndexs.length);
		// props.editTable.deleteTableRowsByIndex(AREA.headTableArea, headIndexs);
	}, 0);

	// let bodyIndexs = [];
	// for (let index = 0; index < props.editTable.getAllRows(AREA.bodyTableArea).length; index++) {
	// 	bodyIndexs.push(index);
	// }
	// props.editTable.deleteTableRowsByIndex(AREA.bodyTableArea, bodyIndexs);
	// props.editTable.addRow(AREA.bodyTableArea);
}
