import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import { createPage, ajax, base } from 'nc-lightapp-front';
import { initTemplate } from './init';
import { AREA, STATUS, URL, BUTTON } from '../constance';
import { initLang, getLangByResId } from '../../pub/tool/multiLangUtil';
import { body_beforeEvent } from './afterEvents';
import {
	onHead_BtnClicks,
	onBody_BtnClicks,
	search_BtnClick,
	onRow_BtnClick,
	searchButton_BtnClick
} from './btnClicks';
import './index.less';
let { NCFormControl } = base;
class List extends Component {
	constructor(props) {
		super(props);
		this.pageid = AREA.pageArea;
		this.headTableid = AREA.headTableArea;
		this.bodyTableid = AREA.bodyTableArea;
		this.state = {
			searchDisable: false,
			msgContent: '',
			value: '',
			index: '',
			headShowCheck: true,
			bodyShowCheck: false
		};
	}

	// 关闭浏览器提示，2018-08-15
	componentWillMount() {
		initLang(this, [ '1014qualitylevel' ], 'uapbd/scmbase', initTemplate.bind(this, this.props));
		window.onbeforeunload = () => {
			let status = this.props.editTable.getStatus(this.headTableid);
			if (status == STATUS.edit) {
				return getLangByResId(this, '1014QUALITYLEVEL-000008'); /* 国际化处理： 当前单据未保存，您确认离开此页面？*/
			}
		};
	}
	componentDidMount() {
		this.query();
	}
	query = () => {
		search_BtnClick.bind(this)(this.props);
	};
	//设置列表肩部信息
	getTableHead = () => {
		return (
			<div className="shoulder-definition-area">
				<div className="definition-search">
					<span className="definition-search-title">
						{getLangByResId(this, '1014QUALITYLEVEL-000010') /* 国际化处理： 质量等级*/}
					</span>
				</div>
				<div className="definition-icons">
					{this.props.button.createButtonApp({
						area: BUTTON.list_body,
						buttonLimit: 3,
						onButtonClick: onBody_BtnClicks.bind(this)
					})}
				</div>
			</div>
		);
	};
	// 根据搜索关键词过滤表头展示信息
	keyWordChange = (value) => {
		this.setState({ value: value }, () => {
			// 调用平台api过滤显示数据
			this.props.editTable.setFiltrateTableData(this.headTableid, [ 'cqlgroupcode', 'cqlgroupname' ], value);
		});
	};
	setHeadBtnEnable = () => {
		let selectLength = this.props.editTable.getCheckedRows(this.headTableid).length;
		let flag = selectLength > 0 ? false : true;
		this.props.button.setDisabled({
			[BUTTON.delete]: flag,
			[BUTTON.print]: flag
		});
	};
	setBodyBtnEnable = () => {
		let selectLength = this.props.editTable.getCheckedRows(this.bodyTableid).length;
		let flag = selectLength > 0 ? false : true;
		this.props.button.setDisabled({
			[BUTTON.deleteLine]: flag
		});
	};
	// 界面组装
	render() {
		let { editTable, button, search, modal } = this.props;
		let { createEditTable } = editTable;

		const { createModal } = modal;
		return (
			<div className="nc-bill-card">
				<div className="nc-bill-header-area">
					<div className="header-title-search-area">
						<h2 className="title-search-detail">
							{getLangByResId(this, '1014QUALITYLEVEL-000010') /* 国际化处理： 质量等级*/}
						</h2>
						<div className="title-search-detail">
							<NCFormControl
								placeholder={getLangByResId(this, '1014QUALITYLEVEL-000009')} /* 国际化处理： 质量等级组编码/名称*/
								value={this.state.value}
								onChange={(value) => {
									this.keyWordChange(value);
								}}
								disabled={this.state.searchDisable}
								// onSearch={searchButton_BtnClick.bind(this, this.props)}
								type="search"
							/>
						</div>
					</div>
					<div className="header-button-area">
						{this.props.button.createButtonApp({
							area: BUTTON.list_head,
							buttonLimit: 4,
							onButtonClick: onHead_BtnClicks.bind(this)
						})}
					</div>
				</div>
				<div className="left-right-table">
					<div className="table-area left-table-area">
						<div className="table-header">
							<div className="table-title">
								{getLangByResId(this, '1014QUALITYLEVEL-000011') /* 国际化处理： 质量等级组*/}
							</div>
						</div>
						{createEditTable(this.headTableid, {
							showIndex: true,
							showCheck: this.state.headShowCheck,
							selectedChange: this.setHeadBtnEnable.bind(this),
							onRowClick: onRow_BtnClick.bind(this)
						})}
					</div>
					<div className="table-area right-table-area">
						<div className="table-header">
							<div className="table-title">
								{getLangByResId(this, '1014QUALITYLEVEL-000012') /* 国际化处理： 等级列表*/}
							</div>
							<div className="right-table-buttons">
								{this.props.button.createButtonApp({
									area: BUTTON.list_body,
									onButtonClick: onBody_BtnClicks.bind(this)
								})}
							</div>
						</div>
						<div className="right-table">
							{createEditTable(this.bodyTableid, {
								showIndex: true,
								isAddRow: true,
								showCheck: this.state.bodyShowCheck,
								selectedChange: this.setBodyBtnEnable.bind(this)
								// onBeforeEvent: body_beforeEvent.bind(this)
							})}
						</div>
					</div>

					{/* {this.getTableHead()} */}
					{/* <div>
						<div>
							<span className="aa-title">质量等级</span>
							<span className="aa-button">
								{this.props.button.createButtonApp({
									area: BUTTON.list_body,
									buttonLimit: 3,
									onButtonClick: onBody_BtnClicks.bind(this)
								})}
							</span>
						</div>
						{createEditTable(this.bodyTableid, {
							showCheck: true,
							showIndex: true,
							onBeforeEvent: body_beforeEvent.bind(this)
						})}
					</div> */}
				</div>
				<div>
					{createModal('MessageDlg', {
						// size: 'sm',
						content: this.state.msgContent
					})}
				</div>
			</div>
		);
	}
}
List = createPage({
	billinfo: {
		billtype: 'grid',
		pagecode: AREA.pageArea,
		bodycode: {
			[AREA.headTableArea]: 'editTable',
			[AREA.bodyTableArea]: 'editTable'
		}
	}
	// initTemplate: initTemplate
})(List);
ReactDOM.render(<List />, document.querySelector('#app'));
